.wrapper{
	.brandimg{
		position:relative;
		border-radius:50px;
		width:70px;
		height:70px;
		left:20px;
		top:20px;
	}
}
button a{
	&:hover{
		text-decoration:none;
		color:white;
	}
	color:white;
}
section{
	padding:9% 11%;
	color:#C4DFE6;
}
.navbar-toggler{
	background-color:gold;
	border-color:gold;
}
.navbar-light .navbar-nav .nav-link {
	color:gold;
	&:hover{
		color:#66A5AD;
	}
}
.navbar-light .navbar-nav .nav-link.active{
	color:gold;
	&:hover{
		color:#66A5AD;
	}
}
.thumbnail:hover {
	position:relative;

    //padding: 50px;
	  //background-color: green;
	  transform: scale(2);
	  transition: transform .2s; /* Animation */
	  //width: 200px;
	  //height: 200px;
	  //margin: 0 auto;
}
#home{
    display: grid;
    grid-template-columns: 1fr 1fr;
	color:#66A5AD;
	font-size:3rem;
	//border:3px solid gold;
	//margin:9% 11%;
	//margin:100px 100px 100px 50px;
	img{
		width:100%;
		height:100%;
	}
	button{
		justify-self:start;
	}
	.intro{
	}
	.extra-home{
		font-size:2rem;
		font-weight:bold;

	}
}
#about{
	padding:12% 15% 6%;
	font-size:1.5rem;
	//color:#C4DFE6;
	.tech-list{
		display:grid;
		grid-template-columns:1fr 1fr;
	}
}
#experience{
	padding:3% 15%;
	//color:#C4DFE6;
	//text-align:center;
}
#projects{
	padding:10% 15%;
	//color:#C4DFE6;
	.carousel-item{
		img{
			width:100%;
			height:600px;
			object-fit:contain;
		}
	}
	.carousel-caption{
		h3,p{
			color:gold;
			text-shadow: 2px 2px 8px black;
		}
	}
}
#footer{
	padding:1% 15%;
	text-align:center; 
}
#contact{
	text-align:center;
	padding:9% 30%;
	font-size:1.5rem;
}
.c-highlight{
	color:gold;//#dc3545;
}
.r-highlight{
	color:#66A5AD;//#dc3545;
}
.name{
	color:gold;
}
.highlightme{
	font-size:2.5rem;
	color:gold;
	text-transform: uppercase;
}

h1{
	margin-bottom:50px;
}
.bg-light{
	background-color:transparent !important;
}
body{
	background-color:#003B46;
}

